// Imports
import ___CSS_LOADER_API_SOURCEMAP_IMPORT___ from "../../../../../../../node_modules/css-loader/dist/runtime/sourceMaps.js";
import ___CSS_LOADER_API_IMPORT___ from "../../../../../../../node_modules/css-loader/dist/runtime/api.js";
var ___CSS_LOADER_EXPORT___ = ___CSS_LOADER_API_IMPORT___(___CSS_LOADER_API_SOURCEMAP_IMPORT___);
// Module
___CSS_LOADER_EXPORT___.push([module.id, "\n.selected-type {\n        background-color: transparent;\n        color: #212121;\n        height: 40px;\n}\n.custom-select .items {\n        color: #212121;\n        border-radius: 8px;\n        border: 1px solid #E3E3E3;\n        overflow: hidden;\n        background-color: #FFFFFF;\n        box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n        /* margin-top: 40px; */\n}\n.custom-select .items .option {\n        font-size: 14px;\n        padding-left: 12px;\n}\n.custom-select .items .option:hover {\n        background-color: #EEEEEE;\n        color: #212121;\n}\n.select-release-type i {\n        font-size: 16px;\n        color: #BCBCBC;\n}\n.custom-placeholder:empty:before {\n        color: #BCBCBC !important;\n        font-weight: 400;\n}\n.custom-select.select-release-type .items {\n        position: static !important;\n}\n", "",{"version":3,"sources":["webpack://./src/presentation/vue3/components/dialogs/module/releaseModuleVersion.vue"],"names":[],"mappings":";AAoZI;QACI,6BAA6B;QAC7B,cAAc;QACd,YAAY;AAChB;AACA;QACI,cAAc;QACd,kBAAkB;QAClB,yBAAyB;QACzB,gBAAgB;QAChB,yBAAyB;QACzB,0CAA0C;QAC1C,sBAAsB;AAC1B;AACA;QACI,eAAe;QACf,kBAAkB;AACtB;AACA;QACI,yBAAyB;QACzB,cAAc;AAClB;AACA;QACI,eAAe;QACf,cAAc;AAClB;AACA;QACI,yBAAyB;QACzB,gBAAgB;AACpB;AACA;QACI,2BAA2B;AAC/B","sourcesContent":["<script setup lang=\"ts\">\n    import { onMounted, ref, computed, defineAsyncComponent } from \"vue\";\n    import { IModule, IModel, IModelHistoryInfo } from \"../../../../../domain/model/models\";\n    import CustomSelect from \"../../customSelect.vue\"\n    import CheckboxComp from \"../../checkboxComp.vue\";\n    import { container } from \"../../../../../domain/core/diContainer\";\n    import { IViewModelManager } from \"../../../../../domain/viewModel/IViewModelManager\";\n    import { IViewModel } from \"../../../../../domain/viewModel/IViewModel\";\n    import { IUseCaseExecutor } from \"../../../../../domain/useCase/IUseCaseExecutor\";\n    import { IListMultiModelVersions } from \"../../../../../domain/useCase/IListMultiModelVersions\";\n    import { IDialog } from \"../../../../../domain/presentation/IDialog\";\n    import { IModuleVersionUseCase } from \"../../../../../domain/useCase/IModuleVersionUseCase\";\n    import { ModelContants } from \"../../../../../../../common/qCloudTemp/modelContants\";\n    import StudioInfo from \"../../ui/studioInfoComp.vue\"\n\n    interface IModuleItem {\n        module: IModule;\n    }\n    interface ITableData {\n        id: string;\n        checked?: boolean;\n        name: string;\n        type: IModel[\"modelType\"];\n        version?: string;\n        disabled?: boolean | true;\n    }\n    interface IDisabledOptions {\n        index: number\n        cause: string;\n    }\n    const viewModel = container.resolve<IViewModel>(IViewModel);\n    const viewModelManager = container.resolve<IViewModelManager>(IViewModelManager);\n    const executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\n    const dialog = container.resolve<IDialog>(IDialog);\n    const moduleVersionUseCase = container.resolve<IModuleVersionUseCase>(IModuleVersionUseCase);\n\n\n    const props = defineProps<IModuleItem>();\n    const items = ref<Array<ITableData>>([]);\n    const modelHistories = ref<Map<string, Array<IModelHistoryInfo>>>();\n    const disabledOptions = ref<Array<IDisabledOptions>>([]);\n\n    const emit = defineEmits([\"close\"]);\n    const checkedItemCount = computed(() => items.value.filter(item => item.checked == true).length);\n    const isAnyItemChecked = computed(() => { return checkedItemCount.value > 0 && items.value.length > checkedItemCount.value})\n    const isAllItemsChecked = computed(() => { return items.value.length == checkedItemCount.value });\n    const isThereVersion = computed(() => checkMaxVersionOfAllItems());\n    const releaseTypeOptions = ref([`Major Version (v${getLastRelasedVersion(\"Major\")})`, `Minor Version (v${getLastRelasedVersion(\"Minor\")})`, `Fix Version (v${getLastRelasedVersion(\"Fix\")})`]);\n    const releaseType = ref<'Major' | 'Minor' | 'Fix'>();\n\n    onMounted(async () => {\n        if(props.module.currentVersion) {\n            checkNextModuleVersion();\n        }\n        await initVersions();\n        handleItems();\n    })\n    function close() {\n        emit('close');\n    }\n    function handleItems () {\n        items.value = props.module.children.map((item) => {\n            const maxVersion = getItemMaxVersion(item as IModel)\n            const tableData: ITableData = {\n                id: item.ID,\n                name: item.name,\n                checked:  maxVersion ? true : (item as IModel).isUnmodifiable,\n                disabled: maxVersion ? false : !(item as IModel).isUnmodifiable,\n                type: (item as IModel).modelType,\n                version: (item as IModel).isUnmodifiable ? 'latest' : maxVersion,\n            }\n            return tableData;\n        })\n    }\n    function handleItemCheck(item: ITableData) {\n        item.checked = !item.checked;\n    }\n    function getItemMaxVersion(item: IModel): string | undefined {\n        if (!modelHistories.value?.has(item.ID))\n            return undefined;\n\n        const versions = modelHistories.value.get(item.ID);\n        if (!versions)\n            return undefined;\n\n        const findMaxVersion = (versions: Array<string>) => {\n            const versionNumbers = versions.map(v => {\n                const parts: Array<string> = v.split(\".\");\n                return parseInt(parts[0]) * 10 + parseInt(parts[1]) * 1;\n            })\n\n            versionNumbers.sort((a, b) => b - a);\n\n            const maxVersion = versionNumbers[0];\n            return `${Math.floor(maxVersion / 10)}.${maxVersion % 10}`;\n        }\n\n        return findMaxVersion(versions.map(e => e.version!));\n\n    }\n    async function initVersions() {\n        const modelIDs = Array<string>();\n        viewModelManager.flatMapItems(viewModel.studio.items, (item) => {\n            if (item.objectType != \"model\") { return; }\n            modelIDs.push(item.ID);\n            return item;\n        });\n        modelHistories.value = await executor.executeUseCase<IListMultiModelVersions>(IListMultiModelVersions, { modelIDs });\n    }\n    function checkMaxVersionOfAllItems() {\n        let isThereVersion: boolean = false;\n        items.value.forEach(item => {\n            if(item.version) {\n                isThereVersion = true;\n            }\n        })\n        return isThereVersion;\n    }\n    function handleAllItemCheck() {\n        if(isAnyItemChecked.value || isAllItemsChecked.value) {\n            items.value.forEach(item => {\n                if(!item.disabled) { item.checked = false };\n            })\n        }\n        else if(checkedItemCount.value == 0) {\n            items.value.forEach(item => {\n                if(!item.disabled) { item.checked = true };\n            })\n        }\n    }\n    function versionDialog(item: ITableData) {\n        const clickedNode = { id: item.id, state: { checked: item.checked }, text: item.name, type: item.type, version: item.version };\n        const hasAnyVersion = modelHistories.value?.has(item.id);\n        const options = { clickedNode, cb: (version: string) => { item.version = version } } as any;\n        if (hasAnyVersion) {\n            options.versions = modelHistories.value?.get(item.id)\n        }\n        dialog.showDialog(\n            defineAsyncComponent(() => import(\"../../application/modelVersionDialog.vue\")),\n            { closable: true, title: \"Change Version\", width: \"1080px\", maxHeight: \"500px\", overflow: \"auto\" }, options\n        );\n    }\n    function setReleaseType(type: string) {\n        releaseType.value = (type.split(\" \")[0] as 'Major' | 'Minor' | 'Fix');\n    }\n    function getLastRelasedVersion(incType: string) {\n            const [major, minor, fix] = props.module.currentVersion ? props.module.currentVersion.split(\".\").map(Number) : props.module.lastReleasedVersion ? props.module.lastReleasedVersion.split(\".\").map(Number) : [0,0,0]\n            switch (incType) {\n                case 'Major':\n                    return `${major + 1}.0.0`;\n                case 'Minor':\n                    return `${major}.${minor + 1}.0`;\n                case 'Fix':\n                    return `${major}.${minor}.${fix + 1}`;\n                default: return;\n            }\n    }\n    function checkNextModuleVersion() {\n        executor.execute(async () => {\n            const versionExistence = await moduleVersionUseCase.checkNextModuleVersion(props.module.ID, props.module.currentVersion!);\n            if (versionExistence.majorExist) {\n                disabledOptions.value?.push({index: 0, cause: \"This version already exists\"})\n            } \n            if (versionExistence.minorExist) {\n                disabledOptions.value?.push({index: 1, cause: \"This version already exists\"})\n            } \n            if (versionExistence.fixExist) {\n                disabledOptions.value?.push({index: 2, cause: \"This version already exists\"})\n            } \n        }, {loading: true})\n    }\n\n    function release() {\n        const checkedItems = items.value.filter(item => item.checked);\n        const releaseItems: Array<{ modelID: string, version: string }> = checkedItems.map((item) => {\n                const itemData: { modelID: string, version: string } = {\n                    modelID: item.id,\n                    version: item.version!\n                }\n                return itemData\n        })\n       \n        executor.execute(async () => {\n            await moduleVersionUseCase.releaseModule({moduleID: props.module.ID, moduleVersionIncType: releaseType.value!, models: releaseItems, basedVersion: props.module.currentVersion});\n        }, {loading: true})\n        close();\n    }\n</script>\n<template>\n    <div class=\"container\">\n        <div v-if=\"props.module.currentVersion\" class=\"current-version-text\">\n            <span>Current version: <span style=\"font-weight: 500;\">v{{ props.module.currentVersion }}</span></span>\n            <StudioInfo v-if=\"props.module.currentVersion != props.module.lastReleasedVersion\" style = \"margin-bottom: 20px;\" :infoText=\"'Current version is not the latest version.'\"></StudioInfo>\n        </div>\n        <div style=\"margin-bottom: 12px\">\n            <CheckboxComp style=\"cursor: pointer;\"\n                :minus=\"isAnyItemChecked\" :check=\"isAllItemsChecked\" @click=\"handleAllItemCheck()\" />\n            <div class=\"items-selected-text\">\n                <span class=\"text-number\">{{ checkedItemCount }}</span>\n                of <span class=\"text-number\">{{ items.length }}</span> items selected\n            </div>\n        </div>\n        <div class=\"module-pages\">\n            <div v-for=\"item in items\" :key=\"item.id\" class=\"row-container\">\n                <CheckboxComp class=\"model-checkbox\" :check=\"item?.checked\" :disabled=\"item.disabled\" @click=\"handleItemCheck(item)\"/>\n                <i :class=\"'app-tier-model-icon ' + ModelContants.getModelVisualInfo(item.type, undefined).icon\" :style=\"{ color: ModelContants.getModelVisualInfo(item.type, undefined).color }\"></i>\n                <span class=\"model-name\">{{ item.name }}</span>\n                <span :class=\"['version-text', item.disabled ? 'disabled' : '']\" @click=\"versionDialog(item)\">\n                    {{ item.version ? item.version : 'No version' }} <i :class=\"['mdi mdi-pencil-outline version-icon', item.disabled ? 'disabled' : '']\"></i>\n                </span>\n            </div>\n        </div>\n        <div class=\"release-type\">\n            <label>Type</label>\n            <CustomSelect class=\"select-release-type\" :options=\"releaseTypeOptions\" :placeholder=\"'Select'\"  :selectedClass=\"'selected-type'\" @input=\"setReleaseType\" :disabledOptions=\"disabledOptions\"></CustomSelect>\n        </div>\n        <div class=\"buttons-div\">\n            <button class=\"cancel-button\" @click=\"close()\">Cancel</button>\n            <button class=\"release-button\" :disabled=\"!isThereVersion || checkedItemCount == 0\" @click=\"release()\">RELEASE</button>\n            <span v-if=\"!isThereVersion\" class=\"button-tooltip\" \n                style=\"top: 482px; left: 513px; z-index: 1; width: 142px;\">There are no items ready to release. At least one of your items must be ready to release.\n            </span>\n        </div>\n    </div>\n</template>\n<style scoped>\n    @import url(\"../../../../../../src/styles/IDE3.css\");\n    .container {\n        padding: 32px 20px 20px;\n    }\n    .module-pages {\n        border: 1px solid #E3E3E3;\n        height: 180px;\n        margin-bottom: 20px;\n        overflow: auto;\n    }\n    .row-container {\n        width: 100%;\n        height: 36px;\n        position: relative;\n        background: #ffffff 0% 0% no-repeat padding-box;\n        border: 1px solid #e0e0e0;\n        display: flex;\n        align-items: center;\n    }\n\n    .buttons-div {\n        display: flex;\n        justify-content: end;\n        margin-top: 60px;\n    }\n    .cancel-button {\n        font-weight: 500;\n        font-size: 16px;\n        text-align: center;\n        color: #757575;\n        display: flex;\n        align-items: center;\n        padding: 8px 16px;\n        border-radius: 4px;\n        margin-right: 4px;\n        border: none;\n        background: transparent;\n    }\n    .release-button {\n        font-weight: 500;\n        font-size: 16px;\n        text-align: center;\n        color: #FFFFFF;\n        background: #151233;\n        display: flex;\n        align-items: center;\n        padding: 8px 16px;\n        border-radius: 4px;\n        border: none;\n    }\n    .release-button:disabled {\n        color: #757575;\n    }\n    .select-release-type {\n        width: 100%;\n        font-size: 15px;\n        height: 40px;\n        line-height: 40px;\n        border: 1px solid #BCBCBC;\n        border-radius: 4px;\n        padding: 0px 12px;\n    }\n    .release-type label {\n        font-weight: 500;\n        font-size: 16px;\n        line-height: 24px;\n        color: #151233;\n        margin-bottom: 4px;\n    }\n    .model-checkbox {\n        position: absolute;\n        top: 8px;\n        left: 20px;\n        cursor: pointer;\n    }\n    .model-name {\n        font-size: 14px;\n        position: absolute;\n        left: 65px;\n    }\n    .items-selected-text {\n        color: #757575;\n        font-size: 14px;\n        margin-left: 28px;\n    }\n    .text-number {\n        color: #212121;\n        font-weight: 500;\n    }\n    .current-version-text {\n        font-size:18px;\n        color: #212121;\n        margin-bottom: 20px;\n        line-height: 21px;\n    }\n    .version-text {\n        margin: 10px 7px 10px auto;\n        display: table;\n        font: normal normal normal 12px/16px Roboto;\n        color: #757575;\n        cursor: pointer;\n        user-select: none;\n    }\n\n    .version-text.disabled {\n        pointer-events: none;\n        cursor: pointer;\n    }\n    .release-button:hover + .button-tooltip {\n        visibility: visible;\n        transition: opacity 0.3s ease-out 1s;\n        opacity: 1;\n    }\n    .button-tooltip {\n        visibility: hidden;\n        position: absolute;\n        background: #757575;\n        color: #FFFFFF;\n        font-size: 11px;\n        font-family: 'Roboto';\n        text-align: center;\n        border-radius: 4px;\n        letter-spacing: 0.2px;\n        padding: 6px 8px 6px 8px;\n        top: 56px;\n        left: 63px;\n        opacity: 0;\n    }\n    .app-tier-model-icon {\n        position: absolute;\n        width: 16px;\n        height: 16px;\n        top: 3px;\n        left: 47px;\n    }\n    .version-info {\n        padding: 6px 12px;\n        margin-bottom: 20px;\n        width: 100%;\n        color: #757575;\n        font-size: 14px;\n        background: #F5F5FA;\n        border-radius: 4px;\n        display: flex;\n        align-items: center;\n        margin-top: 20px;\n    }\n    .version-info i {\n        font-size: 24px;\n        color: #151233;\n        margin-right: 8px;\n    }\n    .qjson-color {\n        color: #449dd1;\n    }\n\n    .yaml-color {\n        color: #449dd1;\n    }\n\n    .js-color {\n        color: #449dd1;\n    }\n\n    .process-color {\n        color: #007c77;\n    }\n\n    .bpmn-color {\n        color: #007c77;\n    }\n\n    .entity-designer-color {\n        color: #e9724c;\n    }\n\n</style>\n<style>\n    .selected-type {\n        background-color: transparent;\n        color: #212121;\n        height: 40px;\n    }\n    .custom-select .items {\n        color: #212121;\n        border-radius: 8px;\n        border: 1px solid #E3E3E3;\n        overflow: hidden;\n        background-color: #FFFFFF;\n        box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n        /* margin-top: 40px; */\n    }\n    .custom-select .items .option {\n        font-size: 14px;\n        padding-left: 12px;\n    }\n    .custom-select .items .option:hover {\n        background-color: #EEEEEE;\n        color: #212121;\n    }\n    .select-release-type i {\n        font-size: 16px;\n        color: #BCBCBC;\n    }\n    .custom-placeholder:empty:before {\n        color: #BCBCBC !important;\n        font-weight: 400;\n    }\n    .custom-select.select-release-type .items {\n        position: static !important;\n    }\n</style>"],"sourceRoot":""}]);
// Exports
export default ___CSS_LOADER_EXPORT___;
